import React from 'react'

interface NextImageProps extends React.ImgHTMLAttributes<HTMLImageElement> {
  fill?: boolean
  sizes?: string
  altReal?: string
  src:
    | {
        src: string
        width?: number
        height?: number
        alt?: string
      }
    | string
}

const Image: React.FC<NextImageProps> = ({
  src,
  fill,
  sizes,
  className,
  style,
  altReal,
  ...props
}) => {
  const { width, height, alt, src: imageSrc } = src

  if (fill) {
    return (
      <img
        {...props}
        src={imageSrc}
        alt={altReal ?? alt}
        style={{ width: '100%', height: '100%', objectFit: 'cover', ...style }}
        className={className}
        sizes={sizes}
        onError={(e) => {
          const target = e.currentTarget
          target.onerror = null // prevent infinite loop if fallback also fails
          target.src = '/fallback.jpg' // replace with your fallback image path
        }}
      />
    )
  }

  return (
    <img
      {...props}
      src={imageSrc}
      alt={altReal ?? alt}
      width={width}
      height={height}
      style={{ display: 'block', ...style }}
      className={className}
      sizes={sizes}
    />
  )
}

export default Image
